body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.chat_list_container {
  border: 1px solid red;
  display: flex;
  height: 100%; }

.create_room_button_container {
  height: 60px; }
  .create_room_button_container button {
    width: 80%;
    margin: 5px;
    margin-right: auto;
    margin-left: auto;
    border: none;
    color: black;
    text-align: center;
    text-decoration: none;
    border-radius: 7px;
    background-color: orange; }

.create_room_input_container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  height: 60px;
  width: 90%;
  margin-left: auto !important;
  margin-right: auto !important;
  border: 1px solid orange;
  background-color: orange;
  border-radius: 10px; }
  .create_room_input_container img {
    width: 30px; }
  .create_room_input_container div {
    padding: 0 5px !important; }
  .create_room_input_container .room_name {
    width: 100%;
    background-color: rgba(0, 0, 0, 0);
    color: #000;
    border: none;
    outline: none;
    height: 30px;
    transition: height 1s;
    -webkit-transition: height 1s; }
  .create_room_input_container .create_button_container, .create_room_input_container .cancel_button_container {
    padding: 0 5px !important; }

.invites_box {
  background: #f9a768;
  /* Old browsers */
  /* FF3.6-15 */
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(135deg, #f9a768 0%, #f7a431 51%, #f79400 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f9a768', endColorstr='#f79400',GradientType=1 );
  /* IE6-9 fallback on horizontal gradient */
  border-radius: 7px;
  margin-left: 5px;
  margin-right: 5px;
  text-align: center;
  margin-top: 10px; }
  .invites_box p {
    width: 100%; }

.invite_room {
  padding: 5px;
  margin-bottom: 5px;
  margin-left: 5px;
  margin-right: 5px;
  border-radius: 10px;
  border-top: 2px solid grey;
  border-bottom: 2px solid grey; }

.accept_invite, .reject_invite {
  width: 100%;
  background-color: grey;
  border: none;
  border-radius: 10px;
  margin: 1px; }

.accept_invite {
  border-bottom: 1px solid green;
  background-color: lightgreen; }

.reject_invite {
  border-top: 1px solid red;
  background-color: lightcoral; }

.room_list_joined {
  background: #f9a768;
  /* Old browsers */
  /* FF3.6-15 */
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(135deg, #f9a768 0%, #f7a431 51%, #f79400 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f9a768', endColorstr='#f79400',GradientType=1 );
  /* IE6-9 fallback on horizontal gradient */
  border-radius: 7px;
  margin-left: 5px;
  margin-right: 5px;
  text-align: center;
  margin-top: 10px; }
  .room_list_joined p {
    width: 100%; }

.room {
  padding: 5px;
  margin-bottom: 5px;
  margin-left: 5px;
  margin-right: 5px;
  border-radius: 10px;
  border-top: 2px solid grey;
  border-bottom: 2px solid grey; }

.selected_room {
  border-top: 3px solid green;
  border-bottom: 3px solid green; }
  .selected_room div .select_room {
    background-color: lightgreen; }

.select_room {
  width: 100%;
  background-color: grey;
  border: none;
  border-radius: 10px;
  margin: 1px; }

.container-fluid {
  height: 100vh; }
  .container-fluid .components {
    flex-grow: 5;
    height: 92vh;
    border: 1px solid green; }

.title_bar_container {
  width: 100%;
  min-height: 8vh;
  height: 8vh;
  max-height: 8vh;
  background: #f9a768;
  /* Old browsers */
  /* FF3.6-15 */
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(135deg, #f9a768 0%, #f7a431 51%, #f79400 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f9a768', endColorstr='#f79400',GradientType=1 );
  /* IE6-9 fallback on horizontal gradient */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; }
  .title_bar_container .title {
    font-weight: 700; }
  .title_bar_container .logout_button {
    background-color: orangered;
    position: absolute;
    right: 20px; }


